@charset "UTF-8";
.port1_main {
  margin-top: 145px;
  height: 90vh;
}
.port1_main .port1_main_mainbox {
  display: flex;
  position: relative;
}
.port1_main .port1_main_mainbox .slide {
  position: absolute;
  opacity: 0;
  transition: opacity 1s ease-in-out;
  flex: 1 0 100%; /* 각 슬라이드가 컨테이너의 전체 너비를 차지하도록 설정 */
}
.port1_main .port1_main_mainbox .slide .boxs {
  width: 1400px;
  height: 750px;
  overflow: hidden;
  background-color: rgb(255, 252, 252);
  display: flex;
  justify-content: space-between;
}
.port1_main .port1_main_mainbox .slide .boxs .textbox {
  width: 60%;
  height: 150px;
  position: relative;
  top: 40%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.port1_main .port1_main_mainbox .slide .boxs .textbox p {
  width: 100%;
  font-size: 70px;
  font-weight: bold;
}
.port1_main .port1_main_mainbox .slide .boxs .textbox strong {
  width: 100%;
}
.port1_main .port1_main_mainbox .slide .boxs .textbox button {
  padding: 0 20px;
  border: 1px solid #ccc;
  border-radius: 10px;
  color: #ccc;
}
.port1_main .port1_main_mainbox .slide .boxs .textbox button:hover {
  transition: all 0.3s;
  border-color: rgb(124, 124, 124);
  background-color: rgb(124, 124, 124);
}
.port1_main .port1_main_mainbox .slide .boxs .title {
  white-space: pre-line;
}
.port1_main .port1_main_mainbox .slide .boxs img {
  width: 40%;
}
.port1_main .port1_main_mainbox .slide.on {
  opacity: 1;
}

.ikon_list {
  display: flex;
  justify-content: center;
  padding-bottom: 30px;
}
.ikon_list table {
  border-spacing: 0;
}
.ikon_list table td, .ikon_list table th {
  padding: 10px 30px;
  box-shadow: 0 0 0 2px rgb(24, 178, 206);
  transition: background-color 0.3s ease, border-color 0.3s ease;
}
.ikon_list table td.on {
  background-color: rgb(24, 178, 206);
  color: #fff;
}
.ikon_list .ikon_boxs {
  padding: 10px 30px;
  box-shadow: 0 0 0 2px rgb(24, 178, 206);
  transition: background-color 0.3s ease, border-color 0.3s ease;
}
.ikon_list .ikon_boxs:hover {
  background-color: #e6f7ff;
  border-color: #3399ff;
}
.ikon_list .ikon_boxs.on {
  background-color: rgb(24, 178, 206);
  color: #fff;
}

.interior_list {
  padding-bottom: 50px;
  height: 35vh;
  margin: 0;
  display: flex;
  flex-direction: column;
  align-items: flex-start; /* 텍스트와 요소를 왼쪽으로 정렬 */
  justify-content: center;
  text-align: left; /* 텍스트를 왼쪽 정렬로 변경 */
}
.interior_list p {
  width: 100%;
  font-size: 25px;
  margin: 10px 0;
  font-weight: bold;
}
.interior_list .list_wep {
  position: relative;
  margin-top: 30px;
  display: flex;
  overflow: hidden;
  width: 1400px;
  transition: transform 0.5s;
}
.interior_list .list_wep .interior_list_boxes {
  width: 200px;
  height: 240px;
  border-radius: 15px;
  overflow: hidden;
  margin: 0 5px;
}
.interior_list .list_wep .interior_list_boxes img {
  transition: transform 0.5s;
  width: 100%;
  height: 100%;
}
.interior_list .list_wep .interior_list_boxes img:hover {
  transform: scale(1.1);
}
.interior_list .list_wep .btns {
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
}
.interior_list .list_wep .btns img {
  width: 50px;
  cursor: pointer;
}
.interior_list .interior_sub_title {
  margin-top: 50px;
  font-size: 35px;
}
.interior_list button {
  margin-top: 15px;
  padding: 10px 20px;
  border: 1px solid #ccc;
  border-radius: 10px;
  color: #ccc;
}
.interior_list button:hover {
  transition: all 0.3s;
  border-color: rgb(124, 124, 124);
  background-color: rgb(124, 124, 124);
  color: #fff;
}

.Interior_review {
  padding-bottom: 50px;
  height: 60vh;
  margin: 0;
  display: flex;
  flex-direction: column;
  align-items: flex-start; /* 텍스트와 요소를 왼쪽으로 정렬 */
  justify-content: center;
  text-align: left;
}
.Interior_review .review_title {
  width: 100%;
  font-size: 25px;
  margin: 10px 0;
  font-weight: bold;
}
.Interior_review .Interior_review_list {
  width: 100%;
  display: flex;
  justify-content: space-between;
  min-width: 0px;
}
.Interior_review .Interior_review_list .Interior_review_boxs {
  width: 450px;
  height: 400px;
}
.Interior_review .Interior_review_list .Interior_review_boxs .Interior_review_img {
  overflow: hidden;
  position: relative;
  border-radius: 4px;
}
.Interior_review .Interior_review_list .Interior_review_boxs .Interior_review_img img {
  width: 100%;
  height: 300px;
  transition: transform 0.5s;
}
.Interior_review .Interior_review_list .Interior_review_boxs .Interior_review_img img:hover {
  transform: scale(1.1);
}
.Interior_review .Interior_review_list .Interior_review_boxs .Interior_review_text {
  min-width: 0px;
  height: 100px;
  font-size: 16px;
  line-height: 20px;
  color: rgb(47, 52, 56);
  word-break: keep-all;
  overflow-wrap: break-word;
}
.Interior_review .Interior_review_list .Interior_review_boxs .Interior_review_text p {
  padding: 5px 0;
  color: #000;
  font-weight: bold;
}
.Interior_review .Interior_review_list .Interior_review_boxs .Interior_review_text span {
  color: #858585;
  font-size: 13px;
}

.best {
  padding-bottom: 50px;
  margin: 0;
  display: flex;
  flex-direction: column;
  align-items: flex-start; /* 텍스트와 요소를 왼쪽으로 정렬 */
  justify-content: center;
  text-align: left;
}
.best .best_title {
  width: 100%;
  font-size: 25px;
  margin: 10px 0;
  font-weight: bold;
}
.best .best_list {
  display: flex;
  justify-content: space-between;
  margin-top: 5px;
}
.best .best_list p {
  margin: 0 5px;
  background-color: #ccc;
  color: #fff;
  padding: 5px 10px;
  border-radius: 5px;
  transition: all 0.3s;
  cursor: pointer;
}
.best .best_list p:hover {
  background-color: rgb(24, 178, 206);
  transition: all 0.3s;
}
.best .best_list p.on {
  background-color: rgb(24, 178, 206);
}
.best .best_item_list {
  width: 100%;
  display: flex;
  justify-content: space-around;
}
.best .best_item_list .best_items {
  width: 400px;
  margin-top: 20px;
  cursor: pointer;
}
.best .best_item_list .best_items .item_subtitle {
  display: block;
  margin: 3px 0;
  font-size: 13px;
  color: #858585;
}
.best .best_item_list .best_items .best_items_imgbox {
  width: 100%;
  height: 300px;
  overflow: hidden;
}
.best .best_item_list .best_items .best_items_imgbox img {
  width: 100%;
  height: 100%;
  transition: transform 0.5s;
}
.best .best_item_list .best_items .best_items_imgbox img:hover {
  transform: scale(1.1);
}
.best .best_item_list .best_items .price_list {
  display: flex;
}
.best .best_item_list .best_items .price_list .color_num {
  display: block;
  margin-right: 3px;
  font-size: 20px;
  color: rgb(24, 178, 206);
  font-weight: bold;
}
.best .best_item_list .best_items .price_list .price {
  display: block;
  margin-right: 3px;
  font-size: 20px;
  font-weight: bold;
}
.best .best_item_list .best_items .price_list .price_event {
  font-size: 10px;
  color: #fff;
  padding: 5px;
  border-radius: 5px;
  background-color: rgb(255, 115, 127);
}
.best .best_item_list .best_items .item_title {
  font-size: 14px;
  letter-spacing: 1px;
  margin: 3px 0;
  padding-bottom: 3px;
}
.best .best_item_list .best_items .item_title:hover {
  color: rgb(24, 178, 206);
}
.best .best_item_list .best_items .review {
  margin-top: 8px;
  font-size: 13px;
}
.best .best_item_list .best_items .review .review_color {
  color: rgb(24, 178, 206);
  font-weight: bold;
}
.best .best_item_list .best_items .review span {
  color: #8f8f8f;
  font-weight: bold;
}