.footer {
  width: 100%;
  display: flex;
  justify-content: center;
  background-color: #ffffff;
  border-top: 1px solid #f1f1f1;
  padding-top: 40px;
}
.footer .center {
  width: 60%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.footer .center .contact {
  display: flex;
}
.footer .center .contact h1 {
  color: #026fd6;
  font-size: 20px;
  font-weight: bold;
}
.footer .center .contact .num h2 {
  font-size: 35px;
  color: #000;
  font-weight: bold;
  letter-spacing: 1px;
}
.footer .center .contact .days {
  font-size: 12px;
  margin-top: 2vh;
}
.footer .center .contact .days li {
  display: flex;
  text-align: left;
  width: 100%;
  color: #000;
}
.footer .center .contact .days li p {
  width: 40%;
  margin: 5px 0;
}
.footer .center .contact .days li span {
  margin: 5px 0;
}
.footer .center .contact2 {
  margin-left: 1vw;
  display: flex;
  flex-wrap: wrap;
}
.footer .center .contact2 h1 {
  color: #d68102;
  font-size: 20px;
  font-weight: bold;
}
.footer .center .contact2 .num {
  margin-left: 2vw;
}
.footer .center .contact2 .num h2 {
  font-size: 35px;
  color: #000;
  font-weight: bold;
  letter-spacing: 1px;
}
.footer .center .contact2 .num .days {
  color: #8b7f7f;
  margin-top: 2vh;
}
.footer .center .online {
  margin-left: 4vw;
}
.footer .center .online a {
  text-align: center;
  margin-bottom: 1vh;
  display: flex;
  justify-content: center;
  flex-direction: column;
  color: #fff;
  width: 210px;
  height: 90px;
  align-items: center;
  background-color: #525252;
  border-radius: 15px;
  font-size: 14px;
  transition: all 0.5s;
}
.footer .center .online a:hover {
  background-color: #8b7f7f;
}
.footer .center .bottom {
  width: 100%;
  padding-bottom: 5px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  font-size: 12px;
}
.footer .center .bottom .menu {
  margin-left: 2vw;
}
.footer .center .bottom .menu .list {
  display: flex;
  justify-content: center;
  padding: 5px 0;
}
.footer .center .bottom .menu .list li {
  text-align: center;
  padding-right: 1vw;
  color: #fff;
}
.footer .center .bottom .menu .where {
  margin-top: 1vh;
  width: 100%;
  color: #8b7f7f;
  line-height: 1.5em;
}
.footer .center .bottom .menu .ikon_list {
  display: flex;
}
.footer .center .bottom .menu .ikon_list li {
  margin-right: 2vw;
  width: 30px;
  height: 30px;
}
.footer .center .bottom .menu .ikon_list li img {
  width: 100%;
  height: 100%;
}