header {
  box-sizing: border-box;
  position: relative;
  display: block;
  flex: 0 0 auto;
  height: 100%;
}
header .port1_header {
  position: fixed;
  z-index: 1102;
  transition: top 0.1s;
  top: 0px;
  width: 100vw;
  height: 80px;
  background: #FFFFFF;
  border-bottom: 1px solid #EAEDEF;
  padding-top: 10px;
}
header .port1_header .port_1_gnb {
  height: 50px;
  padding-top: 10px;
  display: flex;
  justify-content: center;
}
header .port1_header .port_1_gnb img {
  width: 100px;
  height: 50px;
}
header .port1_header .port_1_gnb ul {
  line-height: 50px;
  display: flex;
}
header .port1_header .port_1_gnb ul li {
  padding: 0 20px;
}
header .port1_header .port_1_gnb ul li span {
  font-size: 20px;
  font-weight: bold;
  color: #000;
}
header .port1_header .port_1_gnb ul li:hover span {
  transition: all 0.2s;
  color: rgb(24, 178, 206);
}
header .port1_header input {
  align-items: center;
  line-height: 0;
  box-sizing: border-box;
  border: 1px solid #E6E6E6;
  border-radius: 4px;
  height: 40px;
  padding: 0 14px;
  width: 15%;
  margin-top: 5px;
}
header .port1_header input:hover {
  background-color: #F7F9FA;
  transition: all 0.2s;
  border: 1px solid rgb(24, 178, 206);
}
header .port1_sub_header {
  position: fixed;
  z-index: 1102;
  transition: top 0.1s;
  top: 91px;
  width: 100vw;
  height: 50px;
  line-height: 50px;
  background: #FFFFFF;
  border-bottom: 1px solid #EAEDEF;
}
header .port1_sub_header ul {
  display: flex;
  justify-content: center;
}
header .port1_sub_header ul li {
  padding: 0 14px;
  font-weight: bold;
}
header .port1_sub_header ul li a {
  color: rgba(36, 36, 36, 0.8);
}
header .port1_sub_header ul li:hover a {
  transition: all 0.2s;
  color: rgb(24, 178, 206);
}
header .port1_sub_header ul li.on {
  position: relative;
}
header .port1_sub_header ul li.on::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: rgb(24, 178, 206);
}